.header {
  display: flex;
  justify-content: end;
  align-items: center;
  flex-direction: column;
  height: 300px;
  width: 100%;
  margin-bottom: 2rem;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
  background-image: url("../../images/img.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media all and (min-width: 768px) {
    height: 400px;
  }

  &__title {
    padding: 0 10px;
    margin-bottom: 8px;
    font-size: 2rem;
    color: #94b49f;
    background-color: #f8f8f8;
    border-radius: 05rem;
  }

  &__search {
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    width: 70%;
    border: 2px solid #ddd;
    border-radius: 0.5rem;
    background-color: #f8f8f8;
    outline: none;
  }
  &__label {
    @media all and (min-width: 768px) {
      flex-direction: row;
    }
    display: inline;
    font-size: 1rem;
    margin: 0 15px;
  }
  &__text {
    padding: 0.15rem 0.5rem;
    border-radius: 5px;
    border: 1px solid #666;
    outline: none;
  }
}
